//  :
// id :  id
// t : 1,
//  : /follow
//  : /follow?id=32953014&t=1
export function setFollow(id,t){
    let timestamp =new Date().getTime()
    return request({
        url:'/follow',
        params:{
            id,t,timestamp
        }
    })
}




